<template>
  <div id="RememberSquare" class="pb-16">
    
    <Alert ref="Alert"/>

    
    <InteractiveBord @changeSetting="changeSettings()" :score="score" :haveTimeLeft="false" :maxW="'max-w-xl'" ref="InteractiveBord"/>


    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-col max-w-xl mt-3 px-5 text-2xl">
        
        <div v-show="stopClick" class="w-full flex justify-center px-3">
          <div class="relative w-4/5 flex rounded-lg overflow-hidden">
            <span class="w-full pt-3 bg-green-500"></span>
            <span :style="'width:'+timeRunFlashList" class="absolute transition pt-3 bg-red-500"></span>
          </div>
        </div>

        <div class="w-full flex flex-wrap" :class="stopClick ? '':'mt-3'">
          <div class="mt-4 px-2" :class="{'w-1/3 h-24':levelW==0,'w-1/4 h-20':levelW==1,'w-1/5 h-16':levelW==2,'w-1/6 h-12':levelW==3}" v-for="(item,index) in items" :key="index" >
            <div class="relative w-full h-full rounded-lg overflow-hidden" :class="{'flash':item.isFlash}" @click="isclick(index)">
              <div class="mainView bg-red-400 w-full h-full items-center text-center" :class="{'cursor-pointer':!stopClick}"/>
              <div class="overlayRemember inset-0 px-3 w-full h-full" :style="'background-color: '+item.bgColor" :class="{'cursor-pointer':!stopClick}" />
            </div>
          </div>
        </div>

        <div v-show="nextBtn" class="w-full mt-10 flex flex-wrap justify-center items-center">
          <div class="">
            <button class='bg-purple-500 uppercase rounded-lg py-2 px-4 leading-tight focus:outline-none cursor-pointer' @click="nextLevel()">Next</button>
          </div>
        </div>
        
      </div>
    </div>
    
    
  </div>
</template>

<script>
import Alert from '../components/Alert'
import InteractiveBord from '../components/InteractiveBord'
export default {
  name: 'RememberSquare',
  components: {
    Alert,
    InteractiveBord,
  },
  data() {
    return {
      items:[],
      flashList:[],
      userClick:0,

      stopClick:true,
      stopGame:false,

      nextBtn:false,

      timeRunFlashList:"100%",
      timeBar:3000,
      timeBarInterval:null,

      score:0,

      level:0,
      levelW:0,
      gameOverSound:require("../assets/sounds/GameOver.ogg"),
      
      colors:{"Right":"#44337a","Wrong":"#ff0000","Remaining":"#7668a2"}
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(time=1000){
      clearInterval(this.musab)
      this.addItems();
      this.musab = setTimeout(() => {
        this.addNumberToFlashList();
      }, time);
    },
    isclick(id){
      if(this.stopClick)return;
      if(this.stopGame)return;
      if(this.items[id].isFlash)return;

      this.addFlashClassPart(id)
      var alert = this.$refs.Alert;
      
      if(this.flashList.includes(id)){
        this.userClick++;
      }
      else{
        this.stopClick = true;
        this.stopGame = true;
        this.addFlashClassLoss(id);
        (new Audio(this.gameOverSound)).play();
        alert.alertShow("you Loss","bg-red-500",7000)
        console.log("you Loss")
        return
      }

      if(this.userClick == this.flashList.length){
        this.userClick = 0;
        this.stopClick = true;
        this.nextBtn = true
        this.score += 1;
        alert.alertShow("+1","bg-green-500",300)
        console.log("this Level complet")
      }

    },
    nextLevel(){
      this.level += 1;
      this.nextBtn = false
      this.items.forEach(item => {
        item.isFlash = false
      });
      this.addItems();
      setTimeout(() => {
        this.addNumberToFlashList();
      }, 500);
    },
    addItems(){
      var vm=this
      var num = 0;

      vm.items = [];
      if(vm.level > 10){num = 36;vm.levelW=3;vm.timeBar+=300}
      else if(vm.level > 5){num = 25;vm.levelW=2;vm.timeBar+=200}
      else if(vm.level > 2){num = 16;vm.levelW=1;vm.timeBar+=100}
      else {num = 9;vm.levelW=0}

      for (let i = 0; i < num; i++) {
        vm.items.push({
          isFlash:false,
          bgColor:this.colors.Right
        })        
      }
    },
    addNumberToFlashList(){
      var vm=this;
      vm.flashList = []
      var a = vm.reOrderArray()
      var num = 0;
      num = (vm.levelW+1)*2 + vm.level;


      for (let n = 0; n < num; n++) {
        vm.flashList.push(a[n])
      }

      if(num == vm.items.length){
        vm.stopGame = true;
        vm.addFlashClassAllOpen();
        (this.$refs.Alert).alertShow("YOU WIN :)","bg-green-500",10000);
        console.log("YOU WIN :)")
      }else{ 
        vm.addFlashClassAll(vm.timeBar);
        vm.updateTimeRunFlashList(vm.timeBar);
      }
    },
    reOrderArray(){
      for(var a=[],i=0;i<this.items.length;i++)a[i]=i;
      var temp , current , top = a.length;
      if(top) while (--top) {
        current = Math.floor(Math.random() * (top + 1));
        temp = a[current]
        a[current] = a[top];
        a[top] = temp;
      }
      return a;
    },
    addFlashClassAll(time){
      clearTimeout(this.flashTimeOut)

      this.items.forEach(item => {
        item.isFlash = false
      });

      this.flashList.forEach(id => {
        this.items[id].isFlash = true
      });

      this.flashTimeOut = setTimeout(() => {
        this.items.forEach(item => {
          item.isFlash = false
        });
        this.stopClick = false;
      }, time);
    },
    addFlashClassAllOpen(){
      this.items.forEach(item => {
        item.isFlash = false
      });

      this.flashList.forEach(id => {
        this.items[id].isFlash = true
      });
    },
    addFlashClassPart(id){
      var item = this.items[id];
      if(!item)return;

      item.isFlash = true;
      /* this.playSound(id); */
    },
    addFlashClassLoss(id){
      var item = this.items[id];
      if(!item)return;

      item.bgColor = this.colors.Wrong;

      this.flashList.forEach(itemFlash => {
        if(!this.items[itemFlash].isFlash){
          this.items[itemFlash].bgColor = this.colors.Remaining;
          this.addFlashClassPart(itemFlash)
        }
      });
    },


    updateTimeRunFlashList(timeLimit){
      var time=0,timePass=0;
      clearInterval(this.timeBarInterval);
      this.timeRunFlashList = "100%"

      this.timeBarInterval = setInterval(() => {
        if(timePass >= timeLimit)clearInterval(this.timeBarInterval);
        timePass += 100;
        time = Math.floor((timePass / timeLimit)*100)
        time = 100 - time
        this.timeRunFlashList = time+"%"
        if (time <= 0){
          this.timeRunFlashList = "100%"
        }
      }, 100);

    },

    changeSettings(){
      var vm = this;
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.MStart();
    }
  },
  computed: {
    
  },
}
</script>

<style>
.overlayRemember {
  position: absolute;
  opacity: 0;
  transition: .8s ease;
}

.transition{
  transition: .1s ease;
}

.flash .overlayRemember{
  opacity: .95;
}
</style>
